






import { Vue, Component, Prop } from "vue-property-decorator";

@Component
export default class LanguageBadges extends Vue {
  @Prop(Array) public languages!: string[];

  public asClass(language: string): string {
    return `badge-${language
      .toLocaleLowerCase()
      .replace("#", "sharp")
      .replace("+", "plus")}`;
  }
}
